.container {
  max-width: 100vw;
  margin: 0;
  padding: 1vh;
  padding-top: 1vh;
  border-radius: 8px;
  /* box-shadow: 0 0 10px rgba(0, 0, 0, 0.5); */
  font-size: 0.75vw;
  text-align: center;
  background-color: #eeffff;
}


header {
  text-align: center;
  margin-bottom: 20px;
}

nav {
  margin-bottom: 20px;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
  font-size: 2vw;
}

li {
  display: inline-block;
  margin-right: 10px;
  padding-left: 3vw;
  padding-right: 3vw;
}





hr {
  border: 1px solid #333;
  margin: 20px 0;
}

p {
  font-size: 16px;
  line-height: 1.6;
}